// PAGE COMMON ====================================
.page-header,
.post-header {
    margin-bottom: $spacer;
}

.post-content,
.page-content {
    p,
    ul,
    ol {
        margin-bottom: 1.3rem;
    }
    p+ul {
        margin-top: -$spacer0;
    }
    h2 {
        margin-top: $spacer2;
        margin-bottom: $spacer;
    }
    h3 {
        margin-top: $spacer1;
        margin-bottom: $spacer0;
        color: $gray;
    }
}

@include mq-up(md) {
    .page-header,
    .post-header {
        margin-bottom: $spacer2;
    }
    .post-content,
    .page-content {
        font-size: $fs-md;
        line-height: $lh-md;
        p,
        ul,
        ol {
            margin-bottom: $spacer1;
        }
        h2 {
            margin-top: $spacer4;
            margin-bottom: $spacer1;
        }
        h3 {
            margin-top: $spacer2;
            margin-bottom: $spacer;
            color: $gray;
        }
    }
}

// PAGE HOME ====================================
.select-hide,
.select-show {
    background-color: $gray-lighter;
    color: $gray-lighter;
    white-space: nowrap;
    text-decoration: line-through;
}

.select-show {
    color: #fff;
    background-color: $gray-dark;
}

// PAGE ARTICLES & POST ====================================
.post-list {
    list-style: none;
    padding-left: 0;
    margin-top: $spacer2;
    .post-title {
        color: $brand-color;
        margin-bottom: $spacer0;
    }
    .post {
        margin-bottom: $spacer2;
    }
}

.post-link,
.post-link:hover,
.post-link:focus {
    text-decoration: none;
    color: $body-color;
}

.post-date {
    display: block;
    font-family: $font-heading;
    color: $text-muted;
    margin: -$spacer0 0 $spacer;
    font-size: .875rem;
}

// Reset the responsive content
p.post-desc {
    margin-bottom: $spacer;
    margin-top: -$spacer0;
}

// PAGE POST - BAR ------------------
.post .post-header::after,
.post-link::after {
    content: '';
    display: block;
    width: $spacer4;
    height: 5px;
    background: $brand-light-color;
}

.post-link::after {
    -webkit-transform-origin: left center;
    transform-origin: left center;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    transition: .5s all cubic-bezier(.5, -.2, .25, 1.2);
}

.post-link:focus::after,
.post-link:hover::after {
    -webkit-transform: scaleX(3);
    transform: scaleX(3);
}

// PAGE WORK & PROJECTS ====================================
.project {
    margin-right: $spacer;
    font-size: 1rem;
}

.project-title {
    margin-bottom: $spacer1;
    a {
        font-weight: bold;
        border-bottom: 5px solid $brand-light-color;
        padding-bottom: $spacer0;
        &:hover,
        &:focus {
            text-decoration: none;
            border-bottom-color: $brand-color;
        }
    }
}

@include mq-up(md) {
    .projects {
        margin-top: $spacer2;
    }
    .project-title {
        margin-bottom: $spacer2;
    }
}

// PAGE ABOUT ====================================
.lead-about {
    color: $dark-blue;
}

.site-profile {
    float: left;
    width: 300px;
    height: 500px;
    margin-top: 1rem;
}

.image {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 240px;
    height: 240px;
}